.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

body {
  background: #f5f5f1 !important;
}

.navbar-inverse {
  background: #222831 !important;
}

.navbar-nav > li > .ask-question {
  padding-top: 14px;
  padding-bottom: 14px;
}

.navbar-nav.navbar-right .ask-question {
  padding-top: 8px;
  margin-right: 3rem;
}

.fa-icon-sm {
  margin-right: 4px;
}

.fa-icon-lg {
  margin-right: 6px;
}

.navbar-header a {
  font-size: 26px;
  font-weight: bolder;
  color: #00adb5 !important;
}

.navbar-nav .nav-link-title {
  font-size: 18px;
  color: #fff !important;
  font-weight: 700;
  cursor: pointer;
}

.dropdown > a {
  font-size: 16px;
}

.user-notify-bell {
  font-size: 18px;
}

.nav-header-avatar img {
  width: 38px;
  height: 38px;
  border-radius: 50%;
}

.bell-badge {
  background-color: #DB2828 !important;
  border-color: #DB2828 !important;
}

@font-face {
  /*font-family: Monoisome-Regular;*/
  /*src: url('../fonts/Monoisome-Regular.ttf');*/
  font-family: Monoid-Italic;
  src: url("../fonts/Monoid-Italic.ttf");
}

.nav-li-login {
  font-size: 16px;
  font-family: Monoid-Italic;
}

/***************** show *********************/
.question-panel {
  margin-top: -22px;
  width: 100%;
  background: #fff;
  margin-bottom: 22px;
}

.question-panel .question-wrapper {
  position: relative;
  padding: 20px 0;
  overflow: hidden;
  background: #fff;
  border-bottom: 1px solid #e7eaf1;
  box-shadow: 0 1px 3px 0 rgba(0, 37, 55, 0.05);
}

.question-panel-main {
  padding: 0 24px;
  box-sizing: border-box;
}

.question-panel-main .question-panel-topics {
  padding: 1px 0;
  margin-right: -2px;
  margin-left: -2px;
  display: block;
}

.question-panel-main .question-panel-title {
  margin-top: 4px;
  margin-bottom: 6px;
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  color: #1e1e1e;
}

.question-panel-main .question-panel-detail {
  font-size: 15px;
  line-height: 27px;
  display: table;
}

.question-panel-detail img {
  max-width: 100%;
}

.question-panel-main .question-panel-footer {
  padding-bottom: 12px;
  margin-top: 8px;
  margin-bottom: -12px;
  background: #fff;
}

.question-panel-footer.question-panel-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1000px;
  height: 100%;
  margin: 0 auto;
}

.panel-heading .header-body {
  color: #111;
  font-size: 18px;
  font-weight: bold;
}

a.topic {
  font-size: 18px;
}

.question-follow {
  margin-bottom: 18px;
}

.question-follow > h2 {
  margin-bottom: 5px;
}

.actions {
  display: block !important;
}

.action-buttons {
  float: right;
}

.action-btn {
  display: inline-block !important;
  box-sizing: border-box !important;
  float: right;
}

.answer-info-panel .answer-content {
  display: table-cell;
  font-size: 16px;
  font-weight: 700;
}

.answer-info-avatar {
  width: 3.0em;
  height: 3.0em;
  display: table-cell;
  padding-right: 1em;
}

.answer-info-panel .answer-info-avatar img {
  width: 3.0em;
  height: 3.0em;
  border-radius: 50%;
}

.media-body .answer-info-avatar img {
  width: 3.0em;
  height: 3.0em;
  border-radius: 50%;
}

.answer-item-name {
  display: table-cell;
}

.panel-body .answer-item {
  border-bottom: 1px solid #f0f2f7;
  padding: 10px;
  margin-top: 0 !important;
}

.panel-body .media:last-child {
  border: none;
}

.answer-item .answer-item-content {
  margin-top: 6px;
}

.answer-item .answer-item-time {
  margin-top: 0.8em;
  margin-bottom: 0.2em;
  font-size: 14px;
  color: #8590a6;
}

.answer-item .answer-item-actions {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 14px;
  color: #8590a6;
}

.answer-item-action {
  margin-left: 20px;
  display: inline-block;
  color: #8590a6;
}

#commentForm .is-naked {
  font-size: 14px;
  color: #8590a6;
  height: 100%;
  margin-right: 10px;
}

.answer-item-actions #commentForm .is-naked {
  font-size: 14px;
  color: #8590a6;
  height: 100%;
  margin-right: 10px;
  margin-left: 24px;
}

.media .user-statics {
  margin-bottom: 10px;
}

.about-question {
  text-align: center;
  margin-bottom: 20px;
}

.about-question > h2 {
  margin-bottom: 4px;
}

.question-empty-panel {
  padding: 20px !important;
  font-size: 24px;
  text-align: center;
  color: #aaa;
}

.question-like {
  cursor: pointer;
}

.question-like.active {
  color: #F2711C;
}

.question-action-btns .action-button {
  display: inline-table;
}

.question-action-btns .action-button:last-child {
  float: right;
}

/***************** index *********************/
.feed-item {
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.feed-item .feed-avatar, .feed-avatar img {
  display: block;
  width: 38px;
  height: 38px;
}

.feed-item .feed-avatar {
  float: left;
  margin: 3px 0 0;
  position: relative;
}

.feed-avatar .top-question {
  position: absolute;
  top: -8px;
  right: -6px;
  padding: 5px 7px !important;
}

.feed-item .feed-avatar img {
  border-radius: 3%;
}

.feed-main {
  margin-left: 48px;
}

.feed-item .feed-content h4 {
  margin-right: 20px;
}

.feed-item .feed-author {
  margin: 6px 0;
}

.feed-item .feed-author > span {
  color: #00989a;
  font-weight: bold;
  margin-right: 8px;
}

.feed-main .feed-item-body {
  position: relative;
  margin-top: 8px;
  display: block;
}

.feed-meta {
  display: block;
}

.feed-meta .feed-meta-panel {
  padding: 5px 0;
}

.feed-meta .feed-meta-panel .meta-item {
  color: #8590a6;
  vertical-align: middle;
  margin-right: 18px;
}

.notifications {
  padding: 14px 14px;
  list-style: none;
  font-size: 16px;
  border-bottom: 1px solid #eee;
}

.notifications > a > span {
  color: #00989a;
  font-weight: 600;
  margin-right: 10px;
}

.nav-message-tabs {
  margin-bottom: 12px;
}

.tab-pane .media.unread {
  background: #fff9ea;
}

.tab-pane .media, .message-list .media {
  padding: 14px 14px;
  border-bottom: 1px solid #eee;
  margin-top: 0 !important;
}

.tab-pane .media .media-body > p > a {
  color: #262626;
}

.tab-pane .media .media-body .media-heading a {
  color: #00989a;
  font-weight: bold;
}

.tab-pane .notifications.unread {
  background: #fff9ea;
}

.profile-header {
  position: relative;
  margin: 14px auto;
}

.profile-header .profile-card {
  margin-top: 14px;
}

.userCover-panel {
  width: 100%;
}

.userCover-panel .userCover {
  osition: relative;
  height: 240px;
  overflow: hidden;
  background: #f7f8fa;
  border-top-right-radius: 1px;
  border-top-left-radius: 1px;
  cursor: pointer;
  position: relative;
}

.userCover .userCover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.userCover:hover .userCover-link {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.header-wrapper {
  position: relative;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
}

.header-wrapper .header-wrapper-main {
  position: relative;
  margin: 0 24px 24px;
}

.header-wrapper .header-wrapper-main .user-profile-avatar {
  position: absolute;
  top: -25px;
  left: 0;
  z-index: 4;
}

.user-profile-avatar .userAvatar {
  vertical-align: top;
  display: inline-block;
  overflow: hidden;
  border: 4px solid #fff;
  border-radius: 8px;
}

.header-wrapper-main .userAvatar img {
  width: 160px;
  height: 160px;
}

.profile-header-content {
  padding-top: 16px;
  padding-left: 32px;
  border-left: 164px solid transparent;
}

.profile-header-content .profile-header-conHeader {
  position: relative;
  padding-right: 106px;
  margin-bottom: 16px;
}

.profile-header-content .profile-header-conHeader h1 > span {
  font-weight: 600;
  color: #00adb5;
}

.profile-header-content .profile-header-conBody {
  overflow: hidden;
  position: relative;
  padding-right: 106px;
}

.profile-header-content .edit-profile-user {
  position: absolute;
  right: 0;
  bottom: 8px;
}

.profile-header-content .edit-profile-user .action-button {
  display: inline-table;
  margin-right: 12px;
}

.profile-header-conBody .ProfileHeader-info {
  font-size: 14px;
  line-height: 20px;
  box-sizing: border-box;
}

.ProfileHeader-infoItem {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 10px;
}

.ProfileHeader-infoItem i {
  color: #9fadc7;
  margin-right: 10px;
}

.profile-header-content .profile-header-conFooter {
  overflow: hidden;
  position: relative;
}

.Profile-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  padding: 0 16px;
  margin: 14px auto;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
}

.profileMain {
  background: #fff;
  border: 1px solid #e7eaf1;
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(0, 37, 55, 0.05);
  box-sizing: border-box;
}

.profileMain-header {
  display: block;
}

.profileMain-header .profileMain-tabs {
  list-style: none;
  margin-bottom: 0 !important;
  padding-left: 0 !important;
  padding: 0 4px;
  border-bottom: 1px solid #f0f2f7;
}

.profileMain-header .profileMain-tabs .item {
  display: inline-block;
  padding: 0 20px;
}

.profileMain-header .profileMain-tabs .item a {
  position: relative;
  display: inline-block;
  padding: 16px 4px 17px;
  font-size: 16px;
  line-height: 22px;
  color: #404040;
  text-align: center;
  cursor: pointer;
}

.profileMain-header .profileMain-tabs .item a.active {
  font-weight: 700;
  color: #00b1b3;
  border-bottom: 3px solid #00b1b3;
}

.profileMain-content .list-item {
  padding: 14px 5px;
  border-bottom: 1px solid #eee;
  margin-top: 0;
  display: flex;
}

.profile-action-buttons {
  position: absolute;
  right: 0px;
  bottom: 0px;
}

/*.profile-action-buttons .action-button{
    margin-left: 10px;
    display: table-cell;
    z-index: 99;
}*/
.list-item img {
  width: 36px;
  height: 36px;
  float: left;
  margin-right: 10px;
  border-radius: 50%;
}

.list-item .name {
  font-weight: 700;
  color: #00b1b3;
  font-size: 16px;
  vertical-align: middle;
  height: 100%;
  line-height: 36px;
}

.list-item .meta-info {
  line-height: 36px;
  color: #999;
  font-size: 16px;
}
